.hamburger {
  display: none;
  margin-top: 5px;
}
.list{
    display: flex;
}
.nav-main{
    background-color: burlywood;
}
.drawer{
    color: burlywood;
}

@media  (min-width: 470px)  and (max-width: 860px) {
    .nav-bar .list {
        display: none;
    }
    .hamburger{
        display: block;
        
    }

}
@media  (min-width: 270px)  and (max-width: 469px) {
    .nav-bar .list {
        display: none;
    }
    .hamburger{
        display: block;
        
    }
    .name{
        display: none;
    }
}

